<!-- 账龄分析表-->
<template>
    <BoxTitle>
        <div class="box" v-for="k in [0,1]">
            <div class="print-btn">
                <el-button v-if="k" type="primary" v-print="'#table-content1'">打 印</el-button>
                <el-button v-else type="primary" v-print="'#table-content'">打 印</el-button>
                <el-button type="default" @click="html2Excel(k)">导 出</el-button>
            </div>
            <div :id="'table-content'+(k?k:'')" ref="dom">
                <div class="item">
                    <div class="scorll">
                        <table class="table">
                            <tr class="noBorder head">
                                <td style="text-align:center" colspan="9">{{heads[k]}}</td>
                            </tr>
                            <tr class="noBorder">
                                <td style="text-align:right" colspan="9">金额单位：万元</td>
                            </tr>
                            <tr>
                                <th rowspan="2">序号</th>
                                <th rowspan="2">业主单位</th>
                                <th rowspan="2">金额</th>
                                <th colspan="5">账龄分析</th>
                                <th rowspan="2">负责人</th>
                            </tr>
                            <tr>
                                <th>当期</th>
                                <th>超期30天</th>
                                <th>超期60天</th>
                                <th>超期90天</th>
                                <th>超期120天</th>
                            </tr>
                            <tr v-for="(item,index) in list[k]">
                                <td>{{index + 1}}</td>
                                <td>{{item.kcShortName}}</td>
                                <td>{{formatMoneyAddComma(item.total)}}</td>
                                <td>{{formatMoneyAddComma(item.currMonth)}}</td>
                                <td>{{formatMoneyAddComma(item.over30)}}</td>
                                <td>{{formatMoneyAddComma(item.over60)}}</td>
                                <td>{{formatMoneyAddComma(item.over90)}}</td>
                                <td>{{formatMoneyAddComma(item.over120)}}</td>
                                <td></td>
                            </tr>
                            <tr>
                                <td></td>
                                <td>合计</td>
                                <td>{{formatMoneyAddComma(allSum[k].total)}}</td>
                                <td>{{formatMoneyAddComma(allSum[k].currMonth)}}</td>
                                <td>{{formatMoneyAddComma(allSum[k].over30)}}</td>
                                <td>{{formatMoneyAddComma(allSum[k].over60)}}</td>
                                <td>{{formatMoneyAddComma(allSum[k].over90)}}</td>
                                <td>{{formatMoneyAddComma(allSum[k].over120)}}</td>
                                <td></td>
                            </tr>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    </BoxTitle>
</template>
<script>
    //组件
    import {
        mapState
    } from 'vuex'
    import BoxTitle from "@/components/BoxTitle.vue";
    import InfoBox from "@/components/InfoBox.vue";
    import {
        parseTime,
        getPrevMonth,
        formatNum,
        formatMoneyAddComma
    } from "@/utils/index.js"
    //接口
    import {
        getZlQuery
    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool.js";
    import Table2Excel from 'table2excel.js'

    export default {
        name: 'useEqManage',
        components: {
            BoxTitle,
            InfoBox
        },
        data() {
            return {
                heads: ['西藏先锋绿能环保科技股份有限公司应收账款账龄分析表', '成都中石油昆仑能源有限公司应收账款账龄分析表'],
                list: [],
                allSum: [{}, {}],
            }
        },

        computed: {
            ...mapState({
                personName: state => state.user.nickName,
            }),
        },
        mounted() {
            this.$nextTick(() => {
                let style = document.createElement('style')
                style.setAttribute("type", "text/css");
                style.setAttribute("id", "tableStyle");
                style.innerHTML = `
                <!--
                    @page {
                        margin:1.54cm 2cm;               
                    }
                -->`;
                this.$refs.dom[0].appendChild(style);
                this.$refs.dom[1].appendChild(style);
            })
            this._getData();
        },
        methods: {
            formatMoneyAddComma,
            parseTime,
            _getData() {
                ['xf', 'kl'].map((org, index) => {
                    getZlQuery({
                        org,
                    }).then(res => {
                        this.allSum[index] = {};
                        this._sum(res.data, this.allSum[index])
                        this.$set(this.list, index, res.data);
                    })
                })
            },
            //统计函数
            _sum(target, allSumOne) {
                target.forEach(item => {
                    if (!allSumOne.total) allSumOne.total = 0;
                    if (!allSumOne.currMonth) allSumOne.currMonth = 0;
                    if (!allSumOne.curr) allSumOne.curr = 0;
                    if (!allSumOne.over30) allSumOne.over30 = 0;
                    if (!allSumOne.over60) allSumOne.over60 = 0;
                    if (!allSumOne.over90) allSumOne.over90 = 0;
                    if (!allSumOne.over120) allSumOne.over120 = 0;
                    allSumOne.total += item.total ? item.total : 0;
                    allSumOne.currMonth += item.currMonth ? item.currMonth : 0;
                    allSumOne.over30 += item.over30 ? item.over30 : 0;
                    allSumOne.over60 += item.over60 ? item.over60 : 0;
                    allSumOne.over90 += item.over90 ? item.over90 : 0;
                    allSumOne.over120 += item.over120 ? item.over120 : 0
                });
                return allSumOne
            },
            //下载excel
            html2Excel(k) {
                new Table2Excel(`#table-content${k?k:''} table`).export(
                    `${this.heads[k]}-${this.parseTime(new Date(),'{y}-{m}-{d} {h}-{i}-{s}')}`);
            },
            //刷新数据
            reloadTable() {
                this._getData();
            }
        },
        activated: function () {
            // 移除任何你不想保留的数据
            this._getData();
        },
    }
</script>
<style scoped lang="scss">
    @media print {
        #table-content {
            zoom: 85%;
            margin-top: 0 !important;
        }

        .head {
            font-size: 18px;
            font-weight: 600;
        }

        .scorll {
            overflow: hidden;
            padding-right: 2px;

        }


        .desc-icon,
        .desc-input {
            display: none !important;
        }

        .desc-print {
            display: inline-block !important;
        }

        .table-h {
            color: #000;
        }

        th,
        td {
            border: .1pt solid !important;
        }

        td,
        th,
        span {
            font-size: 9pt !important;
            padding: 40px 0px !important;
        }

        th,
        td {
            border: .1pt solid !important;
        }

        td,
        span {
            color: #000;
        }

        .table-footer,
        .table-h-list,
        .table-h {
            line-height: 0.3in !important;
        }

        .left {
            margin-left: 3in !important;
        }

        .right {
            margin-right: 2in !important;
        }
    }

    #table-content {
        margin-top: 24px;
    }

    .month-div {
        position: absolute;
        top: 12px;
        right: 24px;
        z-index: 2;

        /deep/ .el-date-editor {
            width: 1.4rem;
        }
    }


    .scorll {
        padding-bottom: 10px;
    }

    .noBorder {
        td {
            border: 0 !important;
        }

        &:hover {
            background-color: transparent !important;
        }
    }

    .box {
        padding-top: 24px;
        position: relative;

        .print-btn {
            position: absolute;
            right: 0;
            top: 0;
        }

        .desc-print {
            display: none;
        }

        .desc-input {
            border: 0;
            outline: none;
            color: #303133;
            font-size: 14px;
            font-family: "PFM" !important;
        }
    }

    th,
    td {
        border: 1px solid #d3d3d3;
    }

    th,
    td {
        text-align: center;
        padding: 10px 15px;
        white-space: nowrap;
    }

    tr:nth-child(even) {
        // background-color: #f2f2f2;
    }

    .table {
        border-collapse: collapse;
        width: 100%;
    }

    .item {
        margin-bottom: 16px;

        .table-h {
            font-size: 16px;
            text-align: center;
            line-height: 40px;

        }

        .table-h-list {
            line-height: 40px;
            display: flex;
            justify-content: space-between;
            font-size: 14px;

            span {
                text-align: left;

                &:last-child {
                    text-align: right;
                }
            }
        }

        .table-footer {
            display: flex;
            justify-content: space-between;
            line-height: 40px;

            .left {
                margin-left: 150px;
            }

            .right {
                margin-right: 100px;
            }
        }
    }
</style>